import * as qiniu from 'qiniu-js'
import qiniuApi from "@/network/api/qiniu.js"

function guid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16 | 0,
            v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

let token = '';

let getToken = () => {
    return new Promise((resolve, reject) => {
        if (token) {
            resolve(token);
        } else {
            qiniuApi.token().then(res => {
                token = res.data;
                resolve(token);
            }).catch(res => {
                reject(res);
            });
        }
    });
};

let getFilePath = (extension) => {
    // sys-image/
    // user-image/
    // user-mp4/
    let e = extension.toLowerCase();
    if (/(.*)\.(gif|jpg|jpeg|png)$/.test(e)) { //根据后缀，判断是否符合图片格式
        return 'user-image/';
    } else if (/(.*)\.(mp4|rmvb|avi|ts)$/.test(e)) { //根据后缀，判断是否符合视频格式
        return 'user-mp4/';
    } else {
        return '';
    }
}

let upload = (obj, next, error, complete) => {

    getToken().then(token => {

        const {
            file
        } = obj;

        let fileName = guid();

        let index = file.name.lastIndexOf('.');
        let ext = index != -1 ? file.name.substr(index) : '';
        let path = getFilePath(ext);
        fileName = path + fileName + (ext ? ext : '');

        // 因人而异，自行解决
        const putExtra = {
            // fname: "", // fname: string，文件原文件名
            // params: {}, // params: object，用来放置自定义变量
            // mimeType: [] || null // mimeType: null || array，用来限制上传文件类型，为 null 时表示不对文件类型限制；限制类型放到数组里： ["image/png", "image/jpeg", "image/gif"]
        };

        const config = {
            // useCdnDomain: true,
            // region: qiniu.region.z2
        }

        // 调用sdk上传接口获得相应的observable，控制上传和暂停
        let observable = qiniu.upload(file, fileName, token.token, putExtra, config);
        let subscription = observable.subscribe(next, error, complete);
        return subscription

    }).catch(res => {
        error(res);
    });
}


// token 找后端，obj 这里指代从 el-upload 接收到的 object
export default {
    upload
}